#content{margin-top:40px;max-width:100vw;overflow-x:hidden}#content p{line-height:24px;margin-block-start:0;margin-block-end:1.8em}#content p a{color:#bf322f}#content p a:link{text-decoration:none}#content p a:hover{text-decoration:underline}@media (max-width: 860px){#content p{margin-left:24px;margin-right:24px}}.section{min-height:100vh;z-index:20;width:55vw;min-width:740px;max-width:992px;padding:0 60px;margin-left:auto;margin-right:auto}@media (max-width: 860px){.section{width:100%;margin:0;padding:0;min-width:0}}h1,h2,h3,h4,h5,h6{font-family:"Lusitana",serif;font-weight:normal;text-align:center;margin:0}@media (max-width: 860px){h1,h2,h3,h4,h5,h6{margin:0 24px}}.title,h1{font-size:4em;padding-top:50px;padding-bottom:8px;border-bottom:1px solid #000;margin:0}@media (max-width: 860px){.title,h1{font-size:2.2em}}h2{font-size:3em}@media (max-width: 860px){h2{font-size:1.4em}}h5{font-size:2em;line-height:1.5em}.subtitle,h6{font-size:2em;padding-top:16px}.subtitle a:link,.subtitle a:visited,.subtitle a:hover,.subtitle a:active,h6 a:link,h6 a:visited,h6 a:hover,h6 a:active{text-decoration:none;color:#000}.subtitle a:hover,.subtitle a:active,h6 a:hover,h6 a:active{color:#bf322f}@media (max-width: 860px){.subtitle,h6{font-size:1.2em}}.has-small-font-size{font-size:.8571em}.has-regular-font-size{font-size:1em}.has-medium-font-size{font-size:1.1429em;line-height:1.45}.has-large-font-size{font-size:1.2857em;line-height:1.4}.has-huge-font-size{font-size:1.4286em;line-height:1.3}.more{font-size:1.1429em}.more a{color:#000}.more a:link{text-decoration:none}#maze-section{height:100%;width:100vw;position:relative;user-select:none;overflow-x:hidden}@media (max-width: 860px){#maze-section{top:0}}#maze-section img{width:150vw;position:absolute;bottom:0;left:0}@media (max-width: 860px){#maze-section img{width:220vw}}#maze-section .scroll-text{z-index:60}#maze-section .scroll-front{z-index:50}#maze-section .scroll-middle{z-index:40}#maze-section .scroll-back{z-index:30}.maze-buttons